@charset "utf-8";
@import "../_sass/mixins";

$footer-text: #666666;
$main-color: #333333;
$accent-color: #00B140;
$white-color: #e6e6e6;
$text-color: #111111;
* {
  margin: 0;
}

#button1 , #button2 {
display:inline-block;
/* additional code */
}

$color-1: #3a2c41; // Blackcurrant
$color-2: #eaf2e3; // Willow Brook
$color-3: #000000; // Black
$color-4: #c0a672; // Gimblet

$violet: hsl(283, 49%, 30%);
$crimson: hsl(230, 7%, 16%);
$dark-blue: hsl(230, 7%, 16%);


@font-face {
  font-family: Biko;
  src: url('../fonts/Biko_Regular.otf');
}

html {
  width: 100%;
  height: 100%;
}

ul, menu, dir {
  padding: 0;
}

h1, h2, h3, h4, h5 {
  font-family: 'Biko', sans-serif;

}

h1.title {
  font-size: 60px;
}

body {
  height: 100%;
  width: 100%;
  background-color: transparent;
  background-size: cover;
  background-attachment: fixed;
  font-family: Roboto, sans-serif;
  color: $text-color; 
}

hr {
  background-color: transparent;
  border-color: rgba(darken($accent-color,20%),.5);
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 20px;
  padding-bottom: 20px;
}

canvas {
  position: fixed;
  background-color: transparent;
  top: 0;
  left: 0;
  z-index: -1;
}

a {
  text-decoration: none;
  color: $text-color;
  &:hover {
    text-decoration:none;
  }
}

a.cta {
    text-align: center;
    background: #fa0;
    color: white;
    padding: 20px 40px;
    text-decoration: none;
    text-transform: uppercase;
    font-size: 1em;
    font-weight: bold;
    display:inline-block;
    box-shadow: 0 8px 0 darken(#fa0, 10%);
    border-bottom: none;
    margin-top: 40px;
    &:hover {
      color: white;
      background: lighten(#fa0, 10%);
      border-bottom: none;
    }
  }


section {
  display: grid;
  grid-template-columns: 1fr 1fr 7fr 1fr;
  grid-row-gap: 1em;
  padding-top: 5vh;
  padding-bottom: 5vh;

  > div {
    grid-column-start: 2;
    grid-column-end: 4;
  }

  p {
    font-size: 18px;
    line-height: 30px;
    a {
      text-decoration: none;
      &:hover {
        color: inherit;
        border-bottom: 6px solid lighten(#fa0, 10%);
      }
    }
  }

  img {
    width: 100%;
  }

  &.section-reverse {
    background-color: white;
    color: #111;
    hr {
      background-color: transparent;
      border-color: rgba(darken($accent-color,20%),.5);
    }
  }

.custom-shape-divider-top-1632093186 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
}

.custom-shape-divider-top-1632093186 svg {
    position: relative;
    display: block;
    width: calc(100% + 1.3px);
    height: 118px;
}

.custom-shape-divider-top-1632093186 .shape-fill {
    fill: #3a2c41;
}

  &.section-accent {



  background: #3a2c41;
    color: $white-color; 
    z-index: 99;
    position: relative;
    h2, h3,h1, small {
    color: hsla(28,74%,63%,1);
    }
    a {
    }
    hr {
      background-color: transparent;
      border-color: rgba(#eee,.5);
    }
  }
}

.button__ticket{
  display: flex;
  justify-content: center;
}

.logo {
  display: flex;
  justify-content: center;
  padding: 25px;
  
  img {
    width: 400px;
  }
  .heart {
    color: #f00;
    font-size: 40px;
    margin: 0 20px;
  }
}

header {
  width: 100%;
  margin: 50px 0px;
}

#intro {
  width: 100%;
  .title {
    grid-column-start: 2;
    grid-column-end: 4;
  }
  h2, h3, p {
    grid-column-start: 3;
    grid-column-end: 4;
  }
}

.intro__text {
  text-align: center;
}

#about {
  p {
    line-height: 1.7em;
  }
}


#faq {
  div.question > p {
    padding-bottom: 5vh;
    color: $text-color; /*#777*/

  }

}

#sponsors {
  li {
    list-style-type: none;
    display: inline-block;
    margin: 10px;
  }
}

#partners {
  p {
    font-weight: bold;

    text-align: center;
    margin: 10px;
    max-width: 240px;
 
  }
  li {
    list-style-type: none;
    display: inline-block;
    margin: 10px;
  }
  img {
    max-width: 240px;
  }
}

#contact {
  li a {
    font-size: 2em;
    color: $white-color;
    padding: 0.4em;
    @include transition(0.5s ease);

    &:hover {
      color: darken($white-color, 10%);
    }

    &:active, &:focus {
      color: $white-color;
    }
  }
}

footer {
  padding-top: 2em;
  width: 100%;
  background-color: whitesmoke;
  p {
    color: $footer-text;
    opacity: 0.5;
    font-size: 100%;

  }

  .red {
    color: red;
  }

  .row {
    margin-bottom: 10px;
  }

  a {
    opacity: 0.5;
    color: $footer-text;
    @include transition(0.5s ease);
    font-size: 70%;

    &:hover {
      opacity: 0.9;
      color: $footer-text;
    }

    &:active, &:focus {
      color: $footer-text;
      opacity: 0.5;
    }

  }

  .text-imp {
    color: darken($footer-text, 100%);
    text-transform: uppercase;
    text-decoration: none;
    opacity: 0.9;
    font-size: 100%;
    &:active, &:focus {
      color: darken($footer-text, 100%);
      opacity: 0.9;
    }

  }
  .social-icons {
    a {
      text-decoration: none;
      padding: 10px;
      font-size: 100%;
    }

  }

}

.speakers {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 80px;
  margin-bottom: 60px;
}
.speaker {
  text-align: center;
  margin-bottom: 40px;
  .avatar {
    max-width: 160px;
    border-radius: 50%;
  }
  .bio {
      text-align: justify;
  text-justify: inter-word;
  }
  .social_media {
    padding-bottom: 20px;

  }
  .social_media__icon{
    margin-left: 15px;
  }

}

.workshops {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 80px;
  margin-bottom: 60px;
}

.workshops__2020 {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-column-gap: 80px;
  margin-bottom: 60px;
}

.workshop {
  text-align: center;
  margin-bottom: 40px;
  .summary {
    text-align: left;
    margin-top: 40px;    
  }
  .social_media {
    padding-bottom: 20px;
  }
}


@media (max-width: 425px) {
.table>tbody>tr>td,.table>tbody>tr>th,.table>tfoot>tr>td,.table>tfoot>tr>th,.table>thead>tr>td,.table>thead>tr>th{
  padding: 0;
  font-size: 10px;

}
}


@media (max-width: 1330px) {
  .speakers {
   grid-template-columns: 1fr 1fr;
  }

  .workshops__2020 {
    grid-template-columns: 1fr 1fr;
   }
}

@media (max-width: 768px) {
  .text-center-xs {
    text-align: center;
  }

  header {
    > div {
      padding-top: 10vh;
    }
    img {
      width: 200px;
      padding-bottom: 20px;
    }

    h1 {
      font-size: 30px;
    }
    h3 {
      font-size: 20px;
    }
  }

  .speakers {
    grid-template-columns: 1fr;
   }
 
   .workshops {
     grid-template-columns: 1fr;
    }
}


*, *:before, *:after {
  box-sizing: border-box;
}

