@keyframes "scaleAnimation" {
  0% {
    transform: translateX(-50%) scale(1);
  }
  50% {
    transform: translateX(-50%) scale(1.25);
  }
  100% {
    transform: translateX(-50%) scale(1);
  }
}
/* FOOTER STYLES
–––––––––––––––––––––––––––––––––––––––––––––––––– */

body {
  font: normal 16px/1.5 "Helvetica Neue", sans-serif;
 }
.intro {
  background: #f45b69;
      margin:0;
    padding: 100px 0;
}
.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 ;
  text-align: center;
}
h1 {
  font-size: 2.5rem;
}
.timeline {
  display: block;
      margin:0;
    padding: 0;
  ul {
    padding:  0;
    li {
      list-style-type: none;
      position: relative;
      width: 6px;
      margin: 0 auto;
      background: #c6e939;
      &::after {
        content: "";
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        width: 30px;
        height: 30px;
        border-radius: 50%;
        background: inherit;
        z-index: 1;
        transition: background 0.5s ease-in-out;
      }
      div {
        position: relative;
        bottom: 0;
        width: 400px;
        padding: 15px;
        background: #f45b69;
        visibility: hidden;
        opacity: 0;
        transition: all 0.5s ease-in-out;
        &::before {
          content: "";
          position: absolute;
          bottom: 7px;
          width: 0;
          height: 0;
          border-style: solid;
        }
      }
      &:nth-child(odd) {
        div {
          left: 45px;
          top: 2px;
          transform: translate3d(200px, 0, 0);
          &::before {
            left: -15px;
            border-width: 8px 16px 8px 0;
            border-color: transparent #f45b69 transparent transparent;
          }
        }
      }
      &:nth-child(even) {
        div {
          left: -439px;
          transform: translate3d(-200px, 0, 0);
          &::before {
            right: -15px;
            border-width: 8px 0 8px 16px;
            border-color: transparent transparent transparent #f45b69;
          }
        }
      }
    }
    li.in-view {
      &::after {
        background: #f45b69;
      }
      div {
        transform: none;
        visibility: visible;
        opacity: 1;
      }
    }
  }
}
time {
  display: block;
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 8px;
}
.timeline-clippy {
  ul {
    li {
      &::after {
        width: 40px;
        height: 40px;
        border-radius: 0;
      }
    }
  }
}
.timeline-rhombus {
  ul {
    li {
      &::after {
        clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
      }
      div {
        &::before {
          bottom: 12px;
        }
      }
    }
  }
}
.timeline-star {
  ul {
    li {
      &::after {
        clip-path: polygon( 50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35% );
      }
    }
  }
}
.timeline-heptagon {
  ul {
    li {
      &::after {
        clip-path: polygon( 50% 0%, 90% 20%, 100% 60%, 75% 100%, 25% 100%, 0% 60%, 10% 20% );
      }
    }
  }
}
.timeline-infinite {
  ul {
    li {
      &::after {
        animation: scaleAnimation 2s infinite;
      }
    }
  }
}
.page-footer {
  position: fixed;
  right: 0;
  bottom: 20px;
  display: flex;
  align-items: center;
  padding: 5px;
  color: black;
  background: rgba(255, 255, 255, 0.65);
  a {
    display: flex;
    margin-left: 4px;
  }
}
@media screen and (max-width: 900px) {
  .timeline {
    ul {
      li {
        div {
          width: 250px;
        }
        &:nth-child(even) {
          div {
            left: -289px;
          }
        }
      }
    }
  }
}
@media screen and (max-width: 600px) {
  .timeline {
    ul {
      li {
        margin-left: 20px;
        div {
          width: calc(100vw - 91px);
        }
        &:nth-child(even) {
          div {
            left: 45px;
            &::before {
              left: -15px;
              border-width: 8px 16px 8px 0;
              border-color: transparent #f45b69 transparent transparent;
            }
          }
        }
      }
    }
  }
}
